<script>
import gsap from 'gsap';
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export default {
    name: 'GreenCars',
    mounted() {

    gsap.from(['.hero-section'], {
        delay: 0,
        duration: 1,
        opacity: 0
      });

    let fadein = gsap.utils.toArray('.section')
    fadein.forEach((item) => {

    let tl = gsap.timeline({
      scrollTrigger: {
      trigger: item,
      start:"top 80%",
      }
    });
    tl.from(item, {
      opacity: 0,
      duration: 1
      });
    });

    }

}
</script>

