<script>
import gsap from 'gsap';
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export default {
    name: 'Odyssey',
    mounted() {

    let fadein = gsap.utils.toArray('.section')
    fadein.forEach((item) => {

    let tl = gsap.timeline({
      scrollTrigger: {
      trigger: item,
      start:"top 80%",
      }
    });
    tl.from(item, {
      opacity: 0,
      duration: 1
      });
    });

